/* ==========================================================================
   Header main menu style - begin
   ========================================================================== */

.eltdf-main-menu {
    @include eltdfRelativeHolderLayout();
    height: 100%;
    width: auto;

    ul {
        list-style: none outside none;
        margin: 0;
        padding: 0;
        text-align: left;

        li {
            position: relative;
            display: inline-block;
            float: left;
            padding: 0;
            margin: 0;

            a {
				font-family: $default-heading-font;
                font-size: 12px;
				line-height: 14px;
				text-transform: uppercase;
                color: #808080;

				> span.item_outer {
					position: relative;
					display: inline-block;

					&:after {
						content: " ";
						@include eltdfAbsoluteHolderLayout();
						position: relative;
						height: 0;
						bottom: 0;
						top: auto;
						border-top: 1px solid currentColor;
						@include eltdfTransition(transform 0.2s ease-out, opacity 0.21s ease-in);
						@include eltdfTransform(scale(0.95));
						opacity: 0;
					}

					.eltdf-menu-description {
						display: block;
						text-transform: none;
						color: $first-main-color;
						margin: 8px 0 0;
					}
				}

                i:before {
                    margin: 0 7px 0 0;
                }

                .menu_icon_wrapper {
                    position: relative;
                    display: inline-block;
                    vertical-align: middle;
                }

                .eltdf-menu-featured-icon {
                    position: relative;
                    top: -4px;
                    left: 5px;
                    color: currentColor;
                    font-size: 10px;
                    margin: 0;

                    &:before {
                        margin: 0;
                    }
                }

				&:hover {
					color: $first-main-color;
				}
            }

			&.eltdf-active-item > a,
			> a:hover {

				span.item_outer {

					&:after {
						@include eltdfTransform(scale(1));
						opacity: 1;
					}
				}
			}
        }
    }

    > ul {
        @include eltdfRelativeHolderLayout();
        height: 100%;

        > li {
            float: left;
            height: 100%;
            box-sizing: border-box;

            > a {
                @include eltdfRelativeHolderLayout();
                height: 100%;
                padding: 0 21px;
				color: $first-main-color;
                font-size: 13px;
                line-height: 23px;
				text-transform: none;
                box-sizing: border-box;

                > span.item_outer {
                    position: relative;
                    display: block;
					top: calc(50% + 1px);
					@include eltdfTransform(translateY(-50%));
					padding: 0 3px;

                    .item_text {
                        position: relative;
                        display: inline-block;
                        vertical-align: top;
                    }
                }
            }

            &.has_sub {

                > a .eltdf-menu-arrow {
					display: none;
                }
            }
        }
    }
}

.eltdf-light-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) {

    .eltdf-main-menu > ul > li {

        > a {
            color: $header-light-color;

            &:hover {
                color: $header-light-hover-color;
            }
        }

        &.eltdf-active-item > a {
            color: $header-light-hover-color;
        }
    }
}

.eltdf-dark-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) {

    .eltdf-main-menu > ul > li {

        > a {
            color: $header-dark-color;

            &:hover {
                color: $header-dark-color;
            }
        }

        &.eltdf-active-item > a {
            color: $header-dark-color;
        }
    }
}

/* Narrow drop down styles
   ========================================================================== */
.eltdf-drop-down {

    .second {
        position: absolute;
        display: block;
        left: 0;
        top: 100%;
        visibility: hidden;
        overflow: hidden;
        opacity: 0;
        z-index: 10;
        @include eltdfTransition(top 0.15s ease-out);

        &.eltdf-drop-down-start {
            visibility: visible;
            overflow: visible;
            opacity: 1;
            @include eltdfTransition(opacity 0.2s ease-out);
        }

        &.right {
            right: 0;
            left: auto;
        }
    
        .inner {
            position: relative;
            display: block;
            padding: 0;
            margin: 0;
            z-index: 997;
            box-shadow: 0 5px 5px 0 rgba(0, 0, 0, 0.03);
    
            ul {
        
                li {
                    display: block;
                    float: none;
                    height: auto;
            
                    .widget {
                        width: 100%;
                        margin: 0 !important;
                    }
            
                    a {
                        display: block;
                        float: none;
                        margin: 0;
                        padding: 7px 24px;
                
                        .item_outer {
                            position: relative;
                            display: inline-block;
                            overflow: hidden;
                            padding: 0 30px 0 0;
                            box-sizing: border-box;
                        }
                    }
            
                    &.current-menu-ancestor > a,
                    &.current-menu-item > a {
                        color: $first-main-color;
                
                        .item_outer {
							padding: 0 10px 0 0;
                    
                            &:before {
                                left: 0;
                            }
                        }
                    }
            
                    &.sub {
                
                        > a {

							&:after {
								position: absolute;
								display: inline-block;
								vertical-align: middle;
								top: auto;
								right: 10px;
								content: '\e04b';
								font-family: 'linea-arrows-10';
								font-size: 17px;
								font-weight: normal;
								font-style: normal;
								text-rendering: auto;
								-webkit-font-smoothing: antialiased;
								-moz-osx-font-smoothing: grayscale;
							}
                        }
                    }
                }
        
                &.right {
            
                    li {
                
                        a {
                    
                            .item_outer {
                                padding: 0 0 0 30px;
                            }
                    
                            &:hover {
                        
                                .item_outer {
                            
                                    &:before {
                                        left: auto;
                                        right: 0;
                                    }
                                }
                            }
                        }
                
                        &.sub {
                    
                            > a {

								&:after {
									right: auto;
									left: 10px;
									content: "\e03f";
								}
                            }
                        }
                
                        a {
                            text-align: right;
                        }
                
                        ul {
                            left: -100%;
                        }
                    }
                }
            }
        }
    }

    li {
        
        &.left_position {
            
            .second {
                left: 0;
            }
        }
        
        &.right_position {
            
            .second {
                left: auto;
                right: 0;
            }
        }
    }

    > ul > li {

        &:hover > .second {
            z-index: 20;
        }
    }

    .narrow .second .inner {

        ul {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            background-color: #fff;
            padding: 20px 0 12px;

            li {
                width: 230px;

                ul {
                    position: absolute;
                    left: 100%;
                    top: -19px;
                    height: auto;
                    opacity: 0;
                    overflow: hidden;
                    z-index: 10;
                    box-shadow: 0 5px 5px 0 rgba(0,0,0,0.03);
                    -webkit-transform: translateZ(0);
                    @include eltdfTransition(opacity .4s ease-out);
                }

                &:hover ul {
                    z-index: 20;
                    opacity: 1;
                }
            }

            &.right li ul {
                left: -100%;
            }
        }
    }
}

/* Wide drop down styles
   ========================================================================== */
.eltdf-drop-down .wide {

    .second {

        .inner {
            text-align: center;
			background-color: #fff;
			box-shadow: 0 5px 5px 0 rgba(0, 0, 0, 0.03);

            ul {

                li {

                    a {
                        padding: 6px 0;
                    }

                    ul {
                        position: relative;
                        display: block;
                        top: 0;
                        left: 0;
                        visibility: visible;

                        li {

                            &:last-child {

                                a {
                                    padding-bottom: 0;
                                }
                            }
                        }
                    }
                }
            }

            > ul {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                width: 1100px;
				padding: 35px 0;
				box-sizing: border-box;

                @media only screen and (min-width: 1400px) {
                    .eltdf-grid-1300 & {
                        width: $grid-width-1300;
                    }
                }

                @media only screen and (min-width: 1300px) {
                    .eltdf-grid-1200 & {
                        width: $grid-width-1200;
                    }
                }

                @media only screen and (min-width: 1200px) {
                    .eltdf-grid-1000 & {
                        width: $grid-width-1000;
                    }
                }

                @media only screen and (min-width: 1024px) {
                    .eltdf-grid-800 & {
                        width: $grid-width-800;
                    }
                }

                > li {
                    float: left;
                    width: 25%;
                    margin: 0;
                    padding: 0 26px 0 53px;
                    box-sizing: border-box;

					&:before {
						position: absolute;
						display: block;
						content: " ";
						left: 0;
						top: 0;
						height: calc(100% - 6px);
						border-left: 1px solid rgba($first-main-color, 0.2);
					}

                    &:nth-child(4n+1) {
                        clear: both;
                    }

					&:first-child {

						&:before {
							border-left: 0;
						}
					}

                    > a {
						padding: 0 0 27px;
                        font-size: 13px;
						color: #030303;
						line-height: 1.1em;

						&:after {
							display: none;
						}

                        &.no_link {
                            cursor: default;
                        }

                        .item_outer {
                            padding: 0;
                            overflow: inherit;

                            &:before,
                            &:after {
                                display: none;
                            }
                        }
                    }

                    &.current-menu-ancestor > a,
                    &.current-menu-item > a {
                        color: $first-main-color;
                    }
                }
            }
        }
    }

    &.left_position,
    &.right_position {

        .second .inner > ul {
            width: auto;
            white-space: nowrap;

            > li {
                float: none;
                display: inline-block;
                vertical-align: top;

                &:nth-child(4n+1) {
                    border-left: none;
                }
            }
        }

        .second ul li {
            width: 200px;
        }
    }
}
/* ==========================================================================
   Header main menu style - end
   ========================================================================== */