/* ==========================================================================
   Sticky header style - begin
   ========================================================================== */

$sticky-header-height: 70px;

.eltdf-dark-sticky-header .eltdf-page-header .eltdf-expanding-menu-opener,
.eltdf-dark-sticky-header .eltdf-page-header .eltdf-main-menu.eltdf-sticky-nav>ul>li {
	color: $header-dark-color !important; //Color can be set inline from widget opener

	> a {
		color: $header-dark-color !important;
	}

	> a:hover,
	&.eltdf-active-item>a {
		color: $header-dark-hover-color !important; //Color can be set inline from widget opener
	}
}

.eltdf-light-sticky-header .eltdf-page-header .eltdf-expanding-menu-opener,
.eltdf-light-sticky-header .eltdf-page-header .eltdf-main-menu.eltdf-sticky-nav>ul>li {
	color: $header-light-color !important; //Color can be set inline from widget opener

	> a {
		color: $header-light-color !important;
	}

	> a :hover,
	&.eltdf-active-item{
		color: $header-light-hover-color !important; //Color can be set inline from widget opener
	}
}

.eltdf-main-menu.eltdf-sticky-nav {
	
	> ul {
		
		> li {
			
			&.eltdf-active-item > a,
			> a:hover {
				color: $header-dark-hover-color;
			}
			
			> a {
				color: $header-dark-color;
				
				&:hover {
					color: $header-dark-hover-color;
				}
			}
		}
	}
}

.eltdf-sticky-header {
	position: fixed;
	top: 0;
	left: 0;
	height: $sticky-header-height;
	width: 100%;
    z-index: 100;
    -webkit-transition: -webkit-transform 0.6s ease;
	-moz-transition: -moz-transform 0.6s ease;
	transition: transform .6s ease;
	-webkit-backface-visibility: hidden;
	@include eltdfTransform(translateY(-100%));
    
    .eltdf-grid {
        height: 100% !important; //override settings for other instances of this div
    }
    
    &.header-appear {
        transform: translateY(0%);
    }
    
    .eltdf-sticky-holder {
	    position: relative;
	    height: 100%;
        z-index: 2;
        background-color: #fff;
        border-bottom: 1px solid $default-border-color;
        box-sizing: border-box;
        
        .eltdf-vertical-align-containers {
            padding: 0 110px;
        }
	
	    .eltdf-logo-wrapper a {
		    max-height: $sticky-header-height;
	    }

		.eltdf-shopping-cart-holder {

			.eltdf-header-cart {

				.eltdf-cart-icon {
					top: 0;
				}
			}
		}
    }
    
    .admin-bar &.header-appear {
        top: 32px;
    }
	
	.eltdf-position-center-inner,
	.eltdf-position-right-inner {
		height: 100%;
	}
	
	.eltdf-sticky-right {
		display: inline-block;
		vertical-align: middle;
	}
	
	.widget {
		display: inline-block;
		vertical-align: middle;
		margin: 0;
	}
}
/* ==========================================================================
   Sticky header style - end
   ========================================================================== */