/* ==========================================================================
   Top header style - begin
   ========================================================================== */

.eltdf-top-bar-background {
	@include eltdfAbsoluteHolderLayout();
	background-color: $default-heading-color;
	z-index: 101;
}

.eltdf-top-bar {
	@include eltdfRelativeHolderLayout();
	height: 46px;
	color: #fff;
	font-size: 11px;
	background-color: $default-heading-color;
	z-index: 111;

	.eltdf-grid {
		height: 100%;

		.eltdf-vertical-align-containers {
			padding-left: 0;
			padding-right: 0;

			.eltdf-top-bar-in-grid-padding & {
				padding: 0 25px;
			}
		}
	}

	.eltdf-vertical-align-containers {
		
		.eltdf-position-left:before,
		.eltdf-position-right:before {
			margin-right: 0px;
		}
	}

	.eltdf-top-bar-widget-area,
	.eltdf-top-bar-widget-area-inner {
		height: 100%;
	}

	.eltdf-top-bar-widget {
		display: table;
		float: left;
		height: 100%;
		padding-right: 24px;

		&:last-child {
			padding-right: 0;
		}
	}

	.eltdf-top-bar-widget-inner {
		display: table-cell;
		vertical-align: middle;
	}
}
/* ==========================================================================
   Top header style - end
   ========================================================================== */