/* ==========================================================================
   Mobile header style - begin
   ========================================================================== */

.eltdf-mobile-header {
	position: relative;
	display: none;
    z-index: 101; //one more thatn the content

    /* #General mobile header styles
    ========================================================================== */
    .eltdf-mobile-header-inner {
        position: relative;
        height: 100px;
        background-color: #fff;
        border-bottom: 1px solid $default-border-color;
        box-sizing: border-box;

        .eltdf-mobile-header-holder {
            position: relative;
            z-index: 2;
            height: 100%;
        }
    }

    .eltdf-grid {
        height: 100%;
    }

    .eltdf-vertical-align-containers {
        padding: 0;
    }

    /* #End of general mobile header styles
   ========================================================================== */

    /* #Mobile logo styles
    ========================================================================== */
    .eltdf-mobile-logo-wrapper {
        
        a {
            display: block;
        }
    
        img {
            height: 100%;
            width: auto;
        }
    }
    /* #End of mobile logo styles
    ========================================================================== */

    /* #Mobile menu opener styles
    ========================================================================== */
    .eltdf-mobile-menu-opener {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		height: 100%;
		float: left;
		padding: 0 21px;
		z-index: 2;

		a {
			display: table;
			height: 100%;
			padding-right: 10px;
		}

		.eltdf-mm-lines {
			position: absolute;
			top: 50%;
			left: 50%;
			width: 35px;
			height: 14px;
			@include eltdfTransform(translateX(-50%) translateY(-50%));

			.eltdf-mm-line {
				position: absolute;
				left: 0;
				height: 1px;
				width: 100%;
				background-color: currentColor;
				border-radius: 12px;
				@include eltdfTransition(.3s ease, top .2s ease-out, background-color .2s ease-out);

				&.eltdf-line-1 {
					top: 0;
					outline: transparent solid 1px;
				}

				&.eltdf-line-2 {
					top: calc(50% - 2px);
					outline: transparent solid 1px;
				}

				&.eltdf-line-3 {
					top: calc(100% - 4px);
					outline: transparent solid 1px;
				}
			}
		}

		&:hover {

			.eltdf-mm-lines {

				.eltdf-mm-line {

					&.eltdf-line-1,
					&.eltdf-line-3 {
						top: calc(50% - 2px);
					}

					&.eltdf-line-2 {
						@include eltdfTransform(rotate(90deg));
					}
				}
			}
		}

		&.opened,
		&:hover {
			color: $first-main-color;
		}
    }
    /* #End of mobile menu opener styles
    ========================================================================== */
	
	/* #Mobile menu navigation styles
    ========================================================================== */
	.eltdf-mobile-nav {
		display: none;
		background-color: #fff;
		border-bottom: 1px solid $default-border-color;
		
		ul {
			padding: 0;
			margin: 0;
			list-style-type: none;
			
			li {
				position: relative;
				padding: 0;
				margin: 0;
				
				a {
					position: relative;
					display: block;
				}
				
				a, h6 {
					margin: 0;
					padding: 10px 0;
					font-family: $default-heading-font;
					font-size: 16px;
					line-height: 26px;
					text-transform: uppercase;
				}
			}
			
			//dropdowns
			ul {
				display: none;
				margin: 0 0 0 20px;
				
				li {
					
					&.current-menu-ancestor > a,
					&.current-menu-item > a {
						color: $first-main-color;
					}
					
					a, h6 {
						font-size: 14px;
						line-height: 24px;
					}
				}
			}
		}
		
		.eltdf-grid > ul {
			padding: 0 0 10px;
			
			> li {
				
				&.eltdf-active-item > a {
					color: $first-main-color;
				}
				
				&.eltdf-active-item > .mobile_arrow > .eltdf-sub-arrow {
					@include eltdfTransform(rotate(90deg));
				}
			}
		}
		
		.mobile_arrow {
			$mobile-arrow-height: 45px;
			
			position: absolute;
			right: 0;
			top: 0;
			width: 50px;
			height: $mobile-arrow-height;
			padding-right: 12px;
			line-height: $mobile-arrow-height;
			font-size: 15px;
			text-align: right;
			
			&:hover {
				cursor: pointer;
			}
			
			.eltdf-sub-arrow {
				@include eltdfTransition(transform 0.2s ease-in-out);
			}
			
			//@TODO remove this line of code when new headers are enabled
			.fa-angle-down {
				display: none; //temporarily
			}
		}
		
		li.eltdf-opened > .mobile_arrow > .eltdf-sub-arrow {
			@include eltdfTransform(rotate(90deg));
		}
	}
	/* #End of mobile menu navigation styles
	========================================================================== */
}
/* ==========================================================================
   Mobile header style - end
   ========================================================================== */

/* ==========================================================================
   Sticky Mobile header style - begin
   ========================================================================== */
.eltdf-sticky-up-mobile-header {
	
    .eltdf-animate-mobile-header {
	    
        .eltdf-mobile-header-inner {
            width: 100%;

            //Added left and right transition values because of sidearea transitions on open/close
            -webkit-transition: -webkit-transform 0.4s ease-in-out, left 0.2s ease, right 0.2s ease;
            -moz-transition: -moz-transform 0.4s ease-in-out, left 0.2s ease, right 0.2s ease;
            transition: transform 0.4s ease-in-out, left 0.2s ease, right 0.2s ease;
            @include eltdfTransform(translateY(-100%));
        }
    }

    .mobile-header-appear {
	    
        .eltdf-mobile-header-inner {
	        position: fixed;
	        top: 0;
	        left: 0;
	        width: 100%;
            @include eltdfTransform(translateY(0));
        }
    }
	
	&.admin-bar {
		
		.mobile-header-appear {
			
			.eltdf-mobile-header-inner {
				@include eltdfTransform(translateY(32px));
			}
		}
	}
}
/* ==========================================================================
   Sticky Mobile header style - end
   ========================================================================== */