.eltdf-dark-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-expanding-menu-opener,
.eltdf-dark-header .eltdf-top-bar .eltdf-expanding-menu-opener {
	color: $header-dark-color !important; //Color can be set inline from widget opener

	&:hover,
	&.eltdf-fm-opened {
		color: $header-dark-hover-color !important; //Color can be set inline from widget opener
	}
}

.eltdf-light-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-expanding-menu-opener,
.eltdf-light-header .eltdf-top-bar .eltdf-expanding-menu-opener {
	color: $header-light-color !important; //Color can be set inline from widget opener

	&:hover,
	&.eltdf-fm-opened {
		color: $header-light-hover-color !important; //Color can be set inline from widget opener
	}
}

.eltdf-header-expanding {

	.eltdf-page-header {

		.eltdf-vertical-align-containers {
			padding: 0 110px;

			.eltdf-position-center-inner {
				height: 100%;
			}

			.eltdf-position-right-inner {
				height: 100%;

				.eltdf-expanding-menu {

					&.eltdf-main-menu {
						width: auto;
					}
				}
			}
		}

		.eltdf-sticky-header {

			.eltdf-vertical-align-containers {

				.eltdf-position-center {
					text-align: center;
				}
			}
		}
	}

	.eltdf-main-menu {

		>ul {

			>li {
				position: relative;
				opacity: 0;
				visibility: hidden;
				backface-visibility: hidden;
				-webkit-transform: translateZ(0);
				-ms-transform: translateZ(0);
				transform: translateZ(0);
				-webkit-transition: opacity .2s ease-out, visibility .2s ease-out;
				transition: opacity .2s ease-out, visibility .2s ease-out;

				&:nth-child(n+1) {
					-webkit-transition-delay: .3s;
					transition-delay: .3s
				}

				&:nth-child(n+2) {
					-webkit-transition-delay: .35s;
					transition-delay: .35s
				}

				&:nth-child(n+3) {
					-webkit-transition-delay: .4s;
					transition-delay: .4s
				}

				&:nth-child(n+4) {
					-webkit-transition-delay: .45s;
					transition-delay: .45s
				}

				&:nth-child(n+5) {
					-webkit-transition-delay: .5s;
					transition-delay: .5s
				}

				&:nth-child(n+6) {
					-webkit-transition-delay: .55s;
					transition-delay: .55s
				}

				&:nth-child(n+7) {
					-webkit-transition-delay: .6s;
					transition-delay: .6s
				}

				&:nth-child(n+8) {
					-webkit-transition-delay: .65s;
					transition-delay: .65s
				}

				&:nth-child(n+9) {
					-webkit-transition-delay: .7s;
					transition-delay: .7s
				}

				&:nth-child(n+10) {
					-webkit-transition-delay: .75s;
					transition-delay: .75s
				}
			}
		}
	}

	&.eltdf-expanding-menu-opened {

		.eltdf-main-menu {

			> ul {

				> li {
					opacity: 1;
					-webkit-transition: opacity .3s ease-out, visibility .3s ease-out;
					transition: opacity .3s ease-out, visibility .3s ease-out;
					visibility: visible;

					&:nth-last-child(n+1) {
						-webkit-transition-delay: .3s;
						transition-delay: .3s
					}

					&:nth-last-child(n+2) {
						-webkit-transition-delay: .35s;
						transition-delay: .35s
					}

					&:nth-last-child(n+3) {
						-webkit-transition-delay: .4s;
						transition-delay: .4s
					}

					&:nth-last-child(n+4) {
						-webkit-transition-delay: .45s;
						transition-delay: .45s
					}

					&:nth-last-child(n+5) {
						-webkit-transition-delay: .5s;
						transition-delay: .5s
					}

					&:nth-last-child(n+6) {
						-webkit-transition-delay: .55s;
						transition-delay: .55s
					}

					&:nth-last-child(n+7) {
						-webkit-transition-delay: .6s;
						transition-delay: .6s
					}

					&:nth-last-child(n+8) {
						-webkit-transition-delay: .65s;
						transition-delay: .65s
					}

					&:nth-last-child(n+9) {
						-webkit-transition-delay: .7s;
						transition-delay: .7s
					}

					&:nth-last-child(n+10) {
						-webkit-transition-delay: .75s;
						transition-delay: .75s
					}
				}
			}
		}
	}

	.eltdf-expanding-menu-opener {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		line-height: 1;
		margin: 0 0 0 21px;

		.eltdf-fm-lines {
			position: relative;
			display: inline-block;
			vertical-align: middle;
			width: 35px;
			height: 14px;

			.eltdf-fm-line {
				position: absolute;
				left: 0;
				height: 1px;
				width: 100%;
				background-color: currentColor;
				border-radius: 12px;
				@include eltdfTransition(.3s ease, top .2s ease-out, background-color .2s ease-out);

				&.eltdf-line-1 {
					top: 0;
					outline: transparent solid 1px;
				}

				&.eltdf-line-2 {
					top: calc(50% - 2px);
					outline: transparent solid 1px;
				}

				&.eltdf-line-3 {
					top: calc(100% - 4px);
					outline: transparent solid 1px;
				}
			}
		}

		&:hover {
			color: $first-main-color !important; //Color can be set inline from widget opener

			.eltdf-fm-lines {

				.eltdf-fm-line {

					&.eltdf-line-1,
					&.eltdf-line-3 {
						top: calc(50% - 2px);
					}

					&.eltdf-line-2 {
						@include eltdfTransform(rotate(90deg));
					}
				}
			}
		}

		&.eltdf-fm-opened {
			color: $first-main-color !important; //Color can be set inline from widget opener

			.eltdf-fm-lines {

				.eltdf-fm-line {

					&.eltdf-line-1 {
						display: none;
					}

					&.eltdf-line-2 {
						top: 50%;
						@include eltdfTransform(rotate(45deg));
					}

					&.eltdf-line-3 {
						top: 50%;
						@include eltdfTransform(rotate(-45deg));
					}
				}
			}
		}
	}

	.eltdf-expanding-menu-opened {

		.eltdf-page-header,
		.eltdf-top-bar {
			visibility: hidden;
		}

		.eltdf-expanding-menu-opener,
		.eltdf-logo-wrapper {
			visibility: visible;
		}

		.eltdf-expanding-menu-holder-outer {
			visibility: visible !important;
			opacity: 1 !important;
		}
	}
}